body {
    box-sizing: border-box;
    background: rgb(238,174,202);
    background: radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(148,187,233,1) 100%);
    margin: 0;
    padding: 0;
}

.wrapper {
    display: flex;
    flex-wrap: wrap; /* Allow items to wrap to next line */
    justify-content: center;
    align-items: center;
    gap: 20px;
    padding: 20px;
}

.wrapper button {
    padding: 10px 20px;
    background-color: cadetblue; /* Corrected spelling */
    color: black;
    font-weight: bold;
    font-size: 20px;
    cursor: pointer;
}

.accordian {
    width: 100%; /* Adjusted width for responsiveness */
    max-width: 600px; /* Limiting maximum width for larger screens */
}

.item {
    background-color: cadetblue;
    border-style: inset;
    margin: 15px;
    padding: 8px 20px;
    text-align: left;
}

.title {
    color: rgb(242, 242, 244);
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
}

.content {
    color: black;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-weight: 700;
    font-size: medium;
    font-style: oblique;
}
